import React, { useEffect, useState } from "react";
import { Button } from "@heroui/react";
import Dots from "../Dots";

const ServiceSolutions = ({ title, items }) => {
  const [activeItem, setActiveItem] = useState(null);

  useEffect(() => {
    if (!activeItem) return undefined;
    const original = document.body.style.overflow;
    document.body.style.overflow = "hidden";
    return () => {
      document.body.style.overflow = original;
    };
  }, [activeItem]);

  const openModal = (item) => {
    setActiveItem(item);
  };

  const closeModal = () => {
    setActiveItem(null);
  };

  const renderModal = () => {
    if (!activeItem) return null;
    const modal = activeItem.modal || {};
    const why = modal.why || activeItem.body;
    const who = modal.who || [];
    const benefits = modal.benefits || [];
    const cta = modal.cta || "Получить консультацию";

    return (
      <div className="serviceModalOverlay" onClick={closeModal}>
        <div className="serviceModalPanel" onClick={(event) => event.stopPropagation()}>
          <div className="serviceModalHeader">
            <h4>{activeItem.title}</h4>
            <button className="serviceModalClose" type="button" onClick={closeModal}>
              ×
            </button>
          </div>
          <div className="serviceModalBody">
            <div className="serviceModalSection">
              <span>Почему важно:</span>
              <p>{why}</p>
            </div>
            {who.length > 0 && (
              <div className="serviceModalSection">
                <span>Для кого подходит:</span>
                <div className="serviceModalTags">
                  {who.map((tag) => (
                    <span key={tag}>{tag}</span>
                  ))}
                </div>
              </div>
            )}
            {benefits.length > 0 && (
              <div className="serviceModalSection">
                <span>Плюсы для бизнеса:</span>
                <div className="serviceModalTags">
                  {benefits.map((tag) => (
                    <span key={tag}>{tag}</span>
                  ))}
                </div>
              </div>
            )}
          </div>
          <Button className="serviceModalCta">{cta}</Button>
        </div>
      </div>
    );
  };

  return (
    <section className="serviceSolutions">
      <div className="sectionTitle">
        <Dots />
        <h3>{title}</h3>
      </div>
      <div className="serviceSolutionsGrid">
        {items.map((item, index) => (
          <article
            key={`${item.title}-${index}`}
            className="serviceSolutionsCard"
            onClick={() => openModal(item)}
            role="button"
            tabIndex={0}
            onKeyDown={(event) => {
              if (event.key === "Enter") {
                openModal(item);
              }
            }}
          >
            <div className="serviceSolutionsCardContent">
              <h4>{item.title}</h4>
              <p>{item.body}</p>
            </div>
            <Button className="serviceSolutionsCardButton" type="button">
              Узнать подробнее
            </Button>
          </article>
        ))}
      </div>
      {renderModal()}
    </section>
  );
};

export default ServiceSolutions;
